.row{
    margin-left: 20px;
    color: white;
   
}
.posters{
    display:flex;
    padding: 20px;
    overflow-x: scroll;
    overflow-y: hidden; 
    overflow: auto;
    
}

.posters::-webkit-scrollbar{
    display: none;
}

.poster{
    max-height: 250px;
    margin-right: 20px;
    cursor: pointer;
    border-radius: 10px;
    
    
}
.smallPosters{
    max-height: 100px;
    margin-right: 20px;
    cursor: pointer;
    border-radius: 5px;
}
.poster:hover{
    transform: scale(1.09) ;
    transition: all .4s ease-out; 
}
.smallPosters:hover{
    transform: scale(1.09);
    transition: all .3s ease-out; 
}